import { createStore, applyMiddleware } from 'redux';
import thunk from 'redux-thunk';

function create(reducer){
    global.store = createStore(reducer, applyMiddleware(thunk));
}

function getData(recipientId, property){
    if(typeof recipientId !== 'undefined'){
        let data = store.getState().userData;
        let userData = data[recipientId];

        if(typeof userData == 'undefined'){
            data[recipientId] = {};
            userData = data[recipientId];
        }

        if(typeof property == 'undefined')
            return userData;
        else{
            if(userData.hasOwnProperty(property)){
                return userData[property];
            }
            else
                return undefined;
        }
    }
    else return undefined;
}


module.exports = {create, getData, store}